.GearEditor {
    display: grid;
    grid-template-rows: auto;
    grid-template-areas: 
      "gearName gearName gearName gearName gearName gearName"
      "gearimage brandimage mainImage subArea subArea subArea"
      "seedArea seedArea seedArea seedArea seedArea seedArea"
      "newGearArea newGearArea newGearArea newGearArea newGearArea newGearArea"
      "control control control control control save";
      justify-items: center;
      align-items: center;
      gap: 5px;
    --brand-width: 64px;
    --gear-width: 128px;
}


.GearImage {
    grid-area: gearimage;
}

.BrandImage {
    grid-area: brandimage;
    justify-content: center;
    width: var(--brand-width);
    height: var(--brand-width);
}

.MainAbility {
    grid-area: mainImage;
    --ability-width: 64px;
}

.abilityImage {
    background-color: black;
    border-radius: 50%;
    width: var(--ability-width);
    height: var(--ability-width);
}

.GearName {
    grid-area: gearName;
}

.Seed {
    grid-area: seedArea;
}

.NewGearArea {
    grid-area: newGearArea;
}

.Subs {
    grid-area: subArea;
    overflow:auto;
    border: 1px solid black;
    width: 100%;
    height: 100%;
    max-height: 310px;
}

.Control {
    grid-area: control;
    justify-self: start;
}

.abilitiyList {
    --ability-width: 32px;
    display: flex;
    gap: 4px;
    flex-flow: row wrap;
    padding: 5px;
}

.abilityHeader {
    font-weight: bold;
    padding-left: 5px;
    padding-bottom: 5px;
}

.Save {
    grid-area: save;
}

.GearList {
    display: flex;
    flex-flow: row  wrap;
    gap: 10px 10px;
}

.GearListElem {
    align-self: auto;
    border: 1px solid black;
}

.Button {
    color: #ffffff;
    background-color: var(--button-color);
    border-color: var(--button-color);
    cursor: pointer;
    text-align: center;
    vertical-align: middle;
    border: 1px solid transparent;
    padding: .375rem .75rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: .25rem;
    margin-right: 10px;
}

.ButtonDelete {
    color: #ffffff;
    background-color: rgb(161, 54, 54);
    border-color: rgb(191, 54, 54);
    cursor: pointer;
    text-align: center;
    vertical-align: middle;
    border: 1px solid transparent;
    padding: .375rem .75rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: .25rem;
    margin-right: 10px;
}

.Button:hover {
    background-color: var(--button-color-hover);
}

.ButtonDelete:hover {
    background-color: rgb(140, 42, 42);
}

.Table {
    border: 1px solid var(--border-color);
    border-collapse: collapse;
    width: 100%;
    color: var(--table-color);
    text-align: left;
}

.TableHead {
    padding: .75rem;
    border: 1px solid var(--border-color);
    text-align: center;
}

.ContentNotFound {
    text-align: center;
    background-color: rgba(0,0,0,.05);
}

.TDEven {
    background-color: var(--even-bg);
    text-align: center;
}

.TDEven2 {
    background-color: var(--even-bg);
    text-align: center;
}

.TDOdd {
    background-color: var(--odd-bg);
    text-align: center;
}

.TableAbility {
    --ability-width: 36px;
}

.BrandImg {
    width: var(--brand-width);
    height: var(--brand-width);
}

.GearImg {
    width: var(--gear-width);
    height: var(--gear-width);
}

@media (max-width: 800px) {
    .GearEditor {
        grid-template-rows: auto;
        grid-template-areas: 
        "gearName"
        "gearimage"
        "brandimage"
        "mainImage"
        "subArea" 
        "seedArea"
        "newGearArea"
        "control"
        "save";
        overflow: auto;
        max-height: 600px;
        --brand-width: 48px;
        --gear-width: 96px;
        font-size: smaller;
    }
    .MainAbility {
        --ability-width: 48px;
    }
    .abilitiyList {
        --ability-width: 24px;
    }
    .TableAbility {
        --ability-width: 24px;
    }
}